@import "../../../../common/style/sass/variables";
@import "../../../../common/style/sass/mixins";


.container{
 @include container();
  .h2{
    @include text(28px,800);
    text-align: center;
    margin-bottom: 30px;
  }
  .devContainer{
    @include flex(row,start,center,wrap);
    gap:30px;
    margin-bottom: 60px;
    .itemContainer{
      flex:0 0 30%;
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2)), #FFFDFD;
      //background-color: white;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 15px;
      transition: all 0.6s;
      &:hover{
        scale: 1.05;
      }
      .img{
        width: 100%;
        min-height: 200px;
        border-radius: 15px;
      }
      .desc{
        @include text(20px,800);
        margin: 20px 0 20px 20px;

      }
      .buttonContainer{
        margin: 20px 0 20px 20px;
      }
    }
  }
}
